section.site-footer {
  background: #e2e4e7;
  margin: 0 !important;
  &.top {
    .container {
      border-bottom: 2px dashed #b7b7b7;
      padding-bottom: calc(70px + 12px); }
    padding: 100px;
    @media screen and (max-width: 768px) {
      padding: 20px; }

    h2.newsletter-heading {
      line-height: 1.5;
      font-size: 2.6rem;
      font-weight: 400;
      @media screen and (max-width: 768px) {
        font-size: 1.8rem;
        text-align: center; } }
    .form.newsletter {
      width: 67%;
      margin-top: 47px;
      input {
        height: 70px;
        padding: 15px; }
      button {
        padding: 7px 42px; } } }

  &.bottom {
    padding: 20px;
    padding-bottom: 100px;

    .is-footer-description {
      font-size: 100%;

      span.logo {
        display: block;
        font-size: 20px;
        font-weight: 700;
        text-align: center;
        margin-bottom: 1em; }

      .multigroup {
        text-decoration: underline; } } } }


.footer-urls.column {
  float: right;
  @media screen and (max-width: 768px) {
    text-align: center;
    margin: 0 auto;
    width: 33%; }

  .footer-header {
    font-weight: 600;
    margin-bottom: 1.3rem;
    font-size: 1.2625rem;
    line-height: 1.4;
    color: #777; }

  ul {
    margin-bottom: 1.25rem;
    padding: 0;

    li {
      margin-bottom: 16px;

      a {
        color: #AAA;
        -webkit-transition: color .25s ease-out;
        -moz-transition: color .25s ease-out;
        -ms-transition: color .25s ease-out;
        -o-transition: color .25s ease-out;
        transition: color .25s ease-out;

        &:hover {
          color: $primary;
          text-decoration: underline; } } } } }


.site-copy {
  display: block;
  clear: both; }
