ul.documentation {
  list-style: none;
  padding: 0;
  text-align: center;

  a.doc {
    text-decoration: none;
    border: 2px solid #cccccc;
    width: 100%;
    height: 100%;
    display: block;
    padding: 14px;
    position: relative;
    cursor: pointer;
    color: $primary;

    &:hover {
      background: #FFF;
      box-shadow: 2px 4px 5px 1px #ccc; } }

  h5 {
    font-weight: 700;
    font-size: 18px;
    letter-spacing: 1px;
    text-transform: uppercase;
    margin-top: 10px; }

  li {
    .icon {
      display: inline-block;
      margin-bottom: 16px;
      text-align: center;
      margin-top: 8px;
      color: $primary;
      transition: all .2s ease;
      background: transparent;
      border: 2px solid;
      width: 100px;
      height: 100px;
      border-radius: 100%;
      line-height: 6rem;
      font-size: 60px; } } }


body.is-documentation {
  padding-left: 250px;
  position: relative;
  transition: all 0.5s;

  .hero.docs {
    position: absolute;
    top: 0;
    left: calc(250px + 30px);
    width: calc(100% - 304px); }


  .hero:not(.docs) {
    padding-top: 30px; }

  .navigation {
    position: fixed;
    width: 250px;
    border-right: 3px solid #999999;
    top: 0;
    left: 0;
    z-index: 3;
    height: 100%;
    transition: all 0.5s;

    &__ul {
      list-style: none;
      display: block;
      width: 100%;
      height: 100%;
      overflow: auto; }

    &__item {
      display: block;

      &--last {
        border-bottom: 2px solid #999999; }

      &:first-child a {
        padding-bottom: 0;
        &:hover {
          background-color: transparent !important; } }

      &--link {
        display: block;
        padding: 7px;
        padding-left: 35px;
        color: #777777;
        font-weight: 600;

        &:hover {
          background: #ffffff; }

        &.is-head {
          font-weight: 700;
          padding: 15px 20px; } } } } }

.navbar-burger {
  margin-right: auto;
  margin-left: 0;

  &.active {
    span:nth-child(1), span:nth-child(3), span:nth-child(2) {
      height: 0;
      position: relative; }
    span:nth-child(2):before {
      font-family: FontAwesome;
      font-weight: 900;
      content: "\f060";
      font-size: 22px;
      top: -15px;
      position: absolute; } } }

p.is-2 {
  -moz-column-gap: 24px;
  column-gap: 24px;
  -webkit-column-gap: 24px;
  -moz-column-count: 2;
  column-count: 2;
  -webkit-column-count: 2;
  text-align: justify; }


@media only screen and (max-width: 438px) {
  body.is-documentation {
    &.active {
      padding-left: 223px !important; }
    .navigation {
      width: 223px;
      font-size: 14px; }
    .navbar-item {
      img {
        max-height: 1rem; } } } }

@media only screen and (max-width: 1088px) {
  body.is-documentation {
    padding: 0;
    &.active {
      padding-left: 250px; }
    aside.navigation {
      left: -250px;
      &.active {
        left: 0; } }
    .hero.docs {
      &.is-active {
        display: none; }

      left: 71px;
      width: 100%;
      .navbar-brand {
        float: left; }
      .navbar-menu {
        display: block;
        float: left;
        width: 70%; }
      .navbar-item {
        display: inline-block; } } } }


.margin-top-m55 {
  margin-top: -55px; }

a.before:before, a.after:after {
  content: "";
  transition: 0.5s all ease;
  -webkit-backface-visibility: hidden {
    backface-visibility: hidden; }
  position: absolute; }

a.before:before {
  top: -0.25em; }

a.after:after {
  bottom: -0.25em; }

a.before:before,a.after:after {
  height: 5px;
  height: 0.35rem;
  width: 0;
  background: $secondary; }

a.before:hover:before,a.after:hover:after {
  width: 100%; }

.square {
  box-sizing: border-box;
  position: relative;
  overflow: hidden; }

.square a {
  position: static;
  font-size: 1rem;
  padding: 0.2em 0.4em; }

.square:before,.square:after {
  content: "";
  box-sizing: border-box;
  transition: 0.25s all ease;
  -webkit-backface-visibility: hidden {
    backface-visibility: hidden; }
  position: absolute;
  width: 5px;
  width: 0.35rem;
  height: 0;
  background: $secondary; }

.square:before {
  left: 0;
  bottom: -0.2em; }

.square:after {
  right: 0;
  top: -0.2em; }

.square a:before {
  left: 0;
  transition: 0.25s all ease; }

.square a:after {
  right: 0;
  transition: 0.25s all ease; }

.square:hover:before,.square:hover:after {
  height: calc(100% + 0.4em); }

.square:hover a:before,.square:hover a:after {
  width: 100%; }



kbd {
  background: $dark;
  color: #fff;
  padding: 0 2px;
  border-radius: 5px;

  &.eg {
    padding: 4px;

    &:after {
      content: ":"; } }

  &.hint-code {
    padding: 2px 9px;
    display: inline-block;
    margin-top: 12px;
    position: relative;

    &:before {
      content: "";
      top: 21px;
      position: absolute;
      z-index: 2;
      width: 7px;
      height: 87px;
      background: $dark;
      left: 0; } } }


table.has-text-centered {
  td,th {
    text-align: center !important; }

  tr {
    td:first-child {
      background-color: $dark;
      color: $white;
      font-weight: 700;
      text-decoration: $white solid underline;
      max-width: 0 !important; }

    td:nth-child(2n) {
      min-width: 40% !important; }

    td:nth-child(3n) {
      max-width: 0 !important; }

    pre {
      margin: 0 !important; } } }
