ul.guides {
  list-style: none;
  padding-left: 0;
  margin: 24px 0;
  counter-reset: b;

  li.guide {
    position: relative;
    padding-left: 60px;
    margin-bottom: 24px;


    &:before {
      content: counter(b,decimal-leading-zero) ".";
      counter-increment: b;
      position: absolute;
      left: 0;
      top: 0;
      color: #606873;
      font-family: Montserrat,sans-serif;
      font-size: 22px;
      width: 42px;
      height: 34px;
      line-height: 34px;
      font-weight: 600; }

    h5 {
      font-weight: 700;
      font-size: 20px;
      line-height: 35px;
      margin-top: 6px;
      margin-bottom: 10px; }

    p {
      margin-top: 0; } } }
