.btns-section {
  .button {
    line-height: 56px;
    padding: 0 40px;

    /** section **/ } }
section.no-border-bottom {
  border-bottom: none; }

section.is-section {
  position: relative;
  padding: 96px 32px;
  z-index: 0;
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  overflow: hidden;

  .container {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;

    &:before {
      display: table;
      content: " "; }

    .section-header {
      text-align: center;
      margin-bottom: 72px;

      p.muted {
        margin-top: -33px; }

      span {
        color: #96a2b2;
        font-family: Montserrat,sans-serif;
        font-size: 14px;
        letter-spacing: 2px;
        text-transform: uppercase;
        text-align: center; }

      h2 {
        font-size: 38px;
        margin-bottom: 48px;
        position: relative;
        text-transform: capitalize; } }

    .features.icon-sided {
      text-align: left;

      h5,p {
        margin-left: 50px; }

      h5 {
        font-weight: 700;
        font-size: 18px;
        letter-spacing: 1px;
        text-transform: uppercase;
        margin-top: 10px; } }

    .features {
      list-style: none;
      margin: 24px 0;
      padding: 0;
      text-align: center;

      li {
        margin-right: 4%;
        margin-bottom: 3%;

        .icon {
          display: inline-block;
          margin-bottom: 16px;
          text-align: center;
          float: left;
          width: 40px;
          height: 30px;
          line-height: 30px;
          margin-top: 8px;
          color: $primary;
          transition: all .2s ease;
          font-size: 25px; } } } } }
