.file-tree {
  background-color: #fafafa;
  margin: 24px 0;
  padding: 20px 10px 10px;

  ul {
    display: none;
    list-style: none;
    padding-left: 25px; }

  & > ul {
    display: block;

    li {
      padding: 4px 0;
      line-height: 24px;
      transition: all .5s;

      &.is-open > ul {
        display: block; }

      &.is-folder > span {
        font-weight: 600;
        font-size: 15px;
        cursor: pointer;
        display: inline-block;
        min-width: 120px;

        &:before {
          content: "\f07b";
          font-family: FontAwesome;
          margin-right: 8px;
          color: #8b949e;
          cursor: pointer;
          display: inline-block;
          width: 18px; } }

      &.is-open>span:before {
        content: "\f07c"; }


      &.is-file.clickable {
        cursor: pointer;
        & > * {
          cursor: pointer; }

        &:hover {
          background: $dark;
          color: $white;
          padding: 10px 15px;
          transform: scale(1.02); } }

      &.is-file > span {
        display: inline-block;
        min-width: 120px;

        &:before {
          content: "\f016";
          font-family: FontAwesome;
          margin-right: 8px;
          color: #bec7d2;
          cursor: pointer;
          display: inline-block;
          width: 18px; } } } }
  i {
    margin-left: 24px;
    display: block;
    font-style: normal;
    font-size: 12px;
    line-height: 20px;
    color: #b4b7b8;
    cursor: default;
    transition: color .4s ease; } }
