.nav {
  width: 100%;
  margin: 0;
  padding: 0;
  background: #FFF;
  @extend %relative;
  border-bottom: 1px solid #d7d7d7;

  &.single {
    border-bottom: 0; }

  ul.nav__ul {
    z-index: 50;
    list-style: none;
    margin: 0;
    padding: 0;

    li.nav__item {
      -webkit-box-flex: 1;
      flex: 1;
      display: -webkit-box;
      display: flex;
      font-weight: 500;
      justify-content: center;
      border-left: 1px solid #d7d7d7;
      border-right: 1px solid transparent;

      &.has-dropdown {
        position: relative; }

      &:first-child {
        border-right: 1px solid #d7d7d7; }

      &:hover {
        background-color: $primary;
        color: #FFF;
        border-right: 1px solid #00989a;
        border-left: 1px solid #00989a;

        &:hover > a {
          color: #FFF; } }



      > a {
        -webkit-box-flex: 1;
        flex: 1;
        display: -webkit-box;
        display: flex;
        -webkit-box-pack: center;
        justify-content: center;
        -webkit-box-align: center;
        align-items: center;
        padding: 14px 0;
        font-weight: 700;
        position: relative;
        color: #777; } } } }


.dropdown-menu {
  position: absolute;
  padding-top: 11px;
  width: 280px;
  right: -30px;
  z-index: 985;
  top: 82%;

  > ul {
    background: #fff;
    border-radius: 0 0 4px 4px;
    padding: 20px 15px;
    border: 1px solid #d7d7d7; }

  &:before {
    border: 1px solid #d7d7d7;
    border-left: 0;
    border-bottom: 0;
    content: " ";
    display: block;
    height: 11px;
    pointer-events: none;
    position: absolute;
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
    width: 11px;
    background: #fff;
    top: 6px;
    right: 50px; }

  li {
    border-bottom: 1px dashed #ecf0f1;
    &.last {
      border-bottom: 0;
      a {
        padding-bottom: 0; } }
    a {
      padding: .7em 0; }
    a:hover {
      color: $primary; } }




  a {
    color: #7d8793;
    text-transform: none; } }

.has-dropdown {
  position: relative; }


.user-section li.dropdown-item a {
  color: #7d8793;

  i.icon {
    margin-left: 18px; }
  &:hover {
    color: $primary; } }
