body, html {
  background-color: $body;
  color: $font;
  margin: 0;
  padding: 0; }

#root .is-flex {
  display: flex; }


.is-primary {
  background-color: $primary; }

.is-secondary {
  background-color: $secondary; }


.is-normal {
  display: block; }


.full-width {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  max-width: initial; }


@media screen and (min-width: 769px) {
  .dont-flex {
    -webkit-box-flex: 0;
    flex-grow: 0; } }

.m-right-2 {
  margin-right: 2em !important; }

.m-left-2 {
  margin-left: 2em !important; }

img {
  max-width: 100%; }

[class*="right ribbon"].label {
  right: calc(-14% + 1rem + 1.2em); }

.secondary.label {
    background-color: $secondary !important;
    color: #FFF; }


p {
  line-height: 30px; }

p.subtitle {
  color: #999;
  line-height: 1.4; }
.nav__item a {
  font-size: 1.2em; }



.inline-list {
  display: block;
  list-style: none;

  li {
    display: inline-block;

    a {
      margin-right: 10px;
      padding: 10px;
      display: block; } } }

body.pre-loading {
  background: rgba(0,0,0, .6); }


@keyframes placeHolderShimmer {
    0% {
        background-position: 468px 0; }

    100% {
        background-position: -468px 0; } }



.content-form {
    padding: 77px;
    margin: 50px;
    padding-left: 60px;
    padding-right: 60px;
    background-color: #fff;
    border-radius: 3px;
    border: 3px solid $primary; }
.site-top-section {
  padding: 20px 55px; }


table td, table th {
  text-align: right !important; }
