h1, h2, h3, h4, h5, h6 {
  font-weight: 600; }

.height-100 {
  height: 100%; }

.width-80 {
  width: 80%; }

.width-100 {
  width: 100%; }

.muted {
  color: #919499; }

.bold {
  font-weight: 700 !important; }

section.series.white {
  background-color: #FFF;
  border-top: 1px solid #f1f1f1; }

.m-bottom-3 {
  margin-bottom: 3em!important; }


.m-top-3 {
  margin-top: 3em!important; }


.hero.is-gray {
  background: -webkit-linear-gradient(135deg,#00b1b3,#1d5086);
  background: linear-gradient(-45deg,#00b1b3,#1d5086);
  color: #fff; }

.vm {
  vertical-align: middle; }

.m-top-14px {
  margin-top: 14px !important; }
.m-bottom-14px {
  margin-bottom: 14px !important; }
.is-bold {
  font-weight: 600 !important; }

.dir-ltr {
  direction: ltr !important; }

section.its-premium-page {
  margin-top: 60px !important;
  h3 {
    padding: 0 20px !important; } }

.title {
  line-height: 1.3em !important;
  font-weight: 400 !important; }

.modal > * {
  font-family: SegoeUI !important;
  font-weight: 400; }

.paypal-card:before {
  content: "\f1f4"; }

.credit-card:before {
  content: "\f09d"; }

a.item.first:hover + a.item:not(.first) {
  border-right: 0px solid transparent !important; }

span.is-danger {
  color: #9F3A38 !important; }

.relative {
  position: relative; }


.columns.is-multiline {
  flex-wrap: wrap; }



.paypal-button.paypal-style-checkout.paypal-size-medium .paypal-button-tag-content, .paypal-button-content span {
  display: none !important;
  opacity: 0 !important; }


.paypal-button.paypal-style-checkout.paypal-size-medium .paypal-button-content img {
    line-height: 1 !important;
    margin-top: 8px !important;
    margin-right: 4px !important; }

.paypal-button.paypal-size-medium .paypal-button-content img {
  width: 75% !important;
  height: auto !important;
  margin: 2px 0 0 1px !important; }

.paypal-button.paypal-style-checkout.paypal-size-medium span.paypal-button-content {
  height: 56px !important;
  max-height: 56px !important; }



.notify-user-to-join-with-us {
  text-align: center;
  color: #fff;
  padding: 12px;
  background-color: $secondary; }


blockquote {
  padding: 10px;
  padding-right: 31px;
  border-right: 5px solid $secondary;
  margin: 18px 18px; }

.hero-head.fixed {
  position: fixed;
  width: 100%;
  top: 0;
  background: #00d1b2;
  z-index: 99; }
